.catalog {
  display: flex;
  flex-wrap: wrap;

  max-width: 944px;
  margin: 0 auto;
  perspective: 400px;

  &__card {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 48px;
    
    transition: transform 300ms, color 300ms;
  }

  &__card:not(:nth-child(4n)) {
    margin-right: 48px;
  }
}

.catalog__card:hover {
  transform: translateZ(40px);
  color: #34568b;
}
